/* OTPModal Component Styles */

.otp-display {
  margin-top: 1rem;
  text-align: center;
}

.otp-display p {
  margin-bottom: 0.5rem;
  color: #666;
  font-size: 0.9rem;
}

.otp-code-display {
  display: flex;
  gap: 0.5rem;
  justify-content: center;
  margin-top: 0.5rem;
}

.otp-digit {
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px solid #25D366;
  border-radius: 4px;
  font-size: 1.2rem;
  font-weight: bold;
  color: #25D366;
  background-color: #e8f7f0;
}

.otp-input-container {
  display: flex;
  gap: 0.5rem;
  justify-content: center;
  margin: 1rem 0;
}

.otp-digit-input {
  width: 40px;
  height: 40px;
  text-align: center;
  font-size: 1.2rem;
  font-weight: bold;
  border: 2px solid #ddd;
  border-radius: 4px;
  outline: none;
  transition: border-color 0.3s;
}

.otp-digit-input:focus {
  border-color: #25D366;
  box-shadow: 0 0 0 2px rgba(37, 211, 102, 0.2);
}

/* Button styles */
.cancel-btn,
.create-btn {
  padding: 0.75rem 1.5rem;
  border: none;
  border-radius: 4px;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s;
}

.cancel-btn {
  background-color: #f5f5f5;
  color: #666;
}

.cancel-btn:hover {
  background-color: #e0e0e0;
}

.create-btn {
  background-color: #25D366;
  color: white;
}

.create-btn:hover {
  background-color: #1fa855;
}
