/* QRModal Component Styles */

.qr-content {
  text-align: center;
  padding: 1rem 0;
}

.qr-placeholder {
  width: 250px;
  height: 250px;
  margin: 0 auto 1.5rem;
  border: 2px solid #eee;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #f9f9f9;
  overflow: hidden;
}

.qr-placeholder img {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
}

.qr-content p {
  margin: 0.5rem 0;
  color: #666;
  font-size: 0.95rem;
}

.qr-content p:first-of-type {
  margin-top: 1rem;
}

/* Button styles */
.create-btn {
  padding: 0.75rem 1.5rem;
  border: none;
  border-radius: 4px;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s;
  background-color: #25D366;
  color: white;
}

.create-btn:hover {
  background-color: #1fa855;
}
