/* Session Detail Page */
.session-detail {
  padding: 2rem 0;
}

.detail-header {
  margin-bottom: 2rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.detail-title-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
}

.detail-title-row h1 {
  margin: 0;
}

.delete-session-btn {
  background-color: #f44336;
  color: white;
  border: none;
  padding: 0.75rem 1.5rem;
  border-radius: 6px;
  cursor: pointer;
  font-size: 0.95rem;
  font-weight: 600;
  transition: all 0.3s;
  white-space: nowrap;
}

.delete-session-btn:hover {
  background-color: #d32f2f;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(244, 67, 54, 0.3);
}

.back-btn {
  background-color: #f5f7fa;
  border: 1px solid #ddd;
  color: #555;
  padding: 0.5rem 1rem;
  border-radius: 4px;
  cursor: pointer;
  font-size: 1rem;
  transition: background-color 0.3s;
  align-self: flex-start;
}

.back-btn:hover {
  background-color: #e8e8e8;
}

.detail-grid {
  display: grid;
  gap: 1.5rem;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
}

.detail-card {
  background: white;
  border-radius: 8px;
  padding: 1.5rem;
  box-shadow: 0 4px 6px rgba(0,0,0,0.05);
}

.detail-card h2 {
  margin-bottom: 1rem;
  color: #2a3a5a;
  font-size: 1.3rem;
}

/* Connection Status */
.status-content {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.connection-badge {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1.25rem;
  border-radius: 6px;
  font-weight: bold;
  font-size: 1.1rem;
  width: fit-content;
}

.connection-badge.connected {
  background-color: #e8f5e9;
  color: #25D366;
}

.connection-badge.disconnected {
  background-color: #ffebee;
  color: #f44336;
}

.status-dot {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  display: inline-block;
}

.connection-badge.connected .status-dot {
  background-color: #25D366;
  animation: pulse 2s infinite;
}

.connection-badge.disconnected .status-dot {
  background-color: #f44336;
}

@keyframes pulse {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
}

.last-activity {
  color: #666;
  font-size: 0.9rem;
}

/* Reconnect Actions */
.reconnect-actions {
  margin-top: 1.5rem;
  padding-top: 1.5rem;
  border-top: 1px solid #eee;
}

.reconnect-actions h3 {
  font-size: 1rem;
  color: #2a3a5a;
  margin-bottom: 0.5rem;
}

.reconnect-buttons {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
  margin-top: 1rem;
}

.reconnect-btn {
  flex: 1;
  min-width: 180px;
  padding: 0.75rem 1rem;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-size: 0.95rem;
  font-weight: 500;
  transition: all 0.3s;
}

.qr-btn {
  background-color: #25D366;
  color: white;
}

.qr-btn:hover {
  background-color: #20b858;
  transform: translateY(-2px);
  box-shadow: 0 4px 8px rgba(37, 211, 102, 0.3);
}

.phone-btn {
  background-color: #2196F3;
  color: white;
}

.phone-btn:hover {
  background-color: #1976D2;
  transform: translateY(-2px);
  box-shadow: 0 4px 8px rgba(33, 150, 243, 0.3);
}

/* API Key */
.api-key-content {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.api-key-box {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  background-color: #f5f7fa;
  padding: 1rem;
  border-radius: 6px;
  border: 1px solid #ddd;
}

.api-key-box code {
  flex: 1;
  font-family: 'Courier New', monospace;
  font-size: 0.9rem;
  color: #333;
  word-break: break-all;
}

.copy-btn {
  background-color: #25D366;
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 4px;
  cursor: pointer;
  font-size: 0.9rem;
  white-space: nowrap;
  transition: background-color 0.3s;
}

.copy-btn:hover {
  background-color: #20b858;
}

.help-text {
  color: #666;
  font-size: 0.85rem;
  margin: 0;
}

/* Webhooks */
.webhooks-card {
  grid-column: 1 / -1;
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
}

/* Webhook Event Toggles */
.webhook-event-toggles {
  margin-bottom: 2rem;
  padding-bottom: 1.5rem;
  border-bottom: 2px solid #eee;
}

.webhook-event-toggles h3 {
  font-size: 1.1rem;
  color: #2a3a5a;
  margin-bottom: 1rem;
}

.toggle-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 1rem;
}

.toggle-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.75rem 1rem;
  background-color: #f5f7fa;
  border-radius: 6px;
  border: 1px solid #ddd;
  transition: all 0.3s;
}

.toggle-item:hover {
  background-color: #e8f5e9;
  border-color: #25D366;
}

.toggle-label {
  font-size: 0.95rem;
  color: #555;
  font-weight: 500;
}

.toggle-btn {
  padding: 0.4rem 1.2rem;
  border: none;
  border-radius: 20px;
  cursor: pointer;
  font-size: 0.9rem;
  font-weight: 600;
  transition: all 0.3s;
  min-width: 60px;
}

.toggle-btn:not(.active) {
  background-color: #ffebee;
  color: #f44336;
}

.toggle-btn.active {
  background-color: #25D366;
  color: white;
}

.toggle-btn:hover {
  transform: scale(1.05);
  box-shadow: 0 2px 8px rgba(0,0,0,0.15);
}

.add-webhook-btn {
  background-color: #25D366;
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 4px;
  cursor: pointer;
  font-size: 0.9rem;
  transition: background-color 0.3s;
}

.add-webhook-btn:hover {
  background-color: #20b858;
}

.webhooks-list {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.webhook-item {
  background-color: #f5f7fa;
  padding: 1rem;
  border-radius: 6px;
  border: 1px solid #ddd;
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
  position: relative;
}

.webhook-url {
  word-break: break-all;
  color: #333;
}

.webhook-events {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.event-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.event-tag {
  background-color: #e3f2fd;
  color: #1976D2;
  padding: 0.25rem 0.75rem;
  border-radius: 12px;
  font-size: 0.85rem;
  font-weight: 500;
}

.delete-webhook-btn {
  background-color: #f44336;
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 4px;
  cursor: pointer;
  font-size: 0.85rem;
  align-self: flex-start;
  transition: background-color 0.3s;
}

.delete-webhook-btn:hover {
  background-color: #d32f2f;
}

.no-webhooks {
  text-align: center;
  color: #999;
  padding: 2rem;
  font-style: italic;
}

/* Webhook Modal */
.webhook-modal {
  max-width: 600px;
}

.webhook-events-selector {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.event-checkbox {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.5rem;
  border: 1px solid #ddd;
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.2s;
}

.event-checkbox:hover {
  background-color: #f5f7fa;
  border-color: #25D366;
}

.event-checkbox input[type="checkbox"] {
  cursor: pointer;
  width: 18px;
  height: 18px;
}

.event-checkbox input[type="checkbox"]:checked + span {
  color: #25D366;
  font-weight: 600;
}

.event-checkbox span {
  font-size: 0.9rem;
  user-select: none;
}

/* Enhanced Event Checkbox with Description */
.event-checkbox-enhanced {
  display: flex;
  align-items: flex-start;
  gap: 0.75rem;
  padding: 0.75rem;
  border: 2px solid #ddd;
  border-radius: 6px;
  cursor: pointer;
  transition: all 0.2s;
  background-color: white;
}

.event-checkbox-enhanced:hover {
  background-color: #f5f7fa;
  border-color: #25D366;
  box-shadow: 0 2px 4px rgba(37, 211, 102, 0.1);
}

.event-checkbox-enhanced input[type="checkbox"] {
  cursor: pointer;
  width: 20px;
  height: 20px;
  margin-top: 2px;
  flex-shrink: 0;
}

.event-info {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
  flex: 1;
}

.event-label {
  font-size: 0.95rem;
  font-weight: 600;
  color: #333;
  user-select: none;
}

.event-description {
  font-size: 0.85rem;
  color: #666;
  user-select: none;
  line-height: 1.3;
}

.event-checkbox-enhanced input[type="checkbox"]:checked ~ .event-info .event-label {
  color: #25D366;
}

.event-checkbox-enhanced input[type="checkbox"]:checked {
  accent-color: #25D366;
}

/* Modal Styles */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  background: white;
  border-radius: 8px;
  max-width: 500px;
  width: 90%;
  max-height: 90vh;
  overflow-y: auto;
  box-shadow: 0 10px 40px rgba(0,0,0,0.2);
}

.qr-modal-content {
  background: white;
  border-radius: 8px;
  max-width: 400px;
  width: 90%;
  box-shadow: 0 10px 40px rgba(0,0,0,0.2);
}

.modal-header {
  padding: 1.5rem;
  border-bottom: 1px solid #eee;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.modal-header h2 {
  margin: 0;
  font-size: 1.5rem;
  color: #2a3a5a;
}

.modal-close {
  background: none;
  border: none;
  font-size: 2rem;
  cursor: pointer;
  color: #999;
  transition: color 0.3s;
  line-height: 1;
  padding: 0;
  width: 30px;
  height: 30px;
}

.modal-close:hover {
  color: #333;
}

.modal-body {
  padding: 1.5rem;
}

.modal-footer {
  padding: 1.5rem;
  border-top: 1px solid #eee;
  display: flex;
  justify-content: flex-end;
  gap: 1rem;
}

.cancel-btn {
  background-color: #f5f7fa;
  border: 1px solid #ddd;
  color: #555;
  padding: 0.75rem 1.5rem;
  border-radius: 4px;
  cursor: pointer;
  font-size: 1rem;
  transition: background-color 0.3s;
}

.cancel-btn:hover {
  background-color: #e8e8e8;
}

.create-btn {
  background-color: #25D366;
  border: none;
  color: white;
  padding: 0.75rem 1.5rem;
  border-radius: 4px;
  cursor: pointer;
  font-size: 1rem;
  transition: background-color 0.3s;
}

.create-btn:hover {
  background-color: #20b858;
}

/* QR Content */
.qr-content {
  padding: 2rem;
  text-align: center;
}

.qr-placeholder {
  background-color: #f5f7fa;
  padding: 2rem;
  border-radius: 8px;
  margin-bottom: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.qr-placeholder img {
  max-width: 200px;
  width: 100%;
}

.qr-content p {
  color: #666;
  margin: 0.5rem 0;
  font-size: 0.9rem;
}

/* OTP Input */
.otp-input-container {
  display: flex;
  gap: 0.5rem;
  justify-content: center;
  margin-bottom: 1rem;
}

.otp-digit-input {
  width: 40px;
  height: 50px;
  text-align: center;
  font-size: 1.5rem;
  font-weight: bold;
  border: 2px solid #ddd;
  border-radius: 4px;
  transition: border-color 0.3s;
}

.otp-digit-input:focus {
  outline: none;
  border-color: #25D366;
}

.otp-display {
  background-color: #f5f7fa;
  padding: 1rem;
  border-radius: 8px;
  margin-top: 1rem;
}

.otp-display p {
  margin: 0 0 0.5rem 0;
  color: #666;
  font-size: 0.9rem;
}

.otp-code-display {
  display: flex;
  gap: 0.5rem;
  justify-content: center;
}

.otp-digit {
  width: 40px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: white;
  border: 2px solid #25D366;
  border-radius: 4px;
  font-size: 1.5rem;
  font-weight: bold;
  color: #25D366;
}

/* Form Group */
.form-group {
  margin-bottom: 1.5rem;
}

.form-group label {
  display: block;
  margin-bottom: 0.5rem;
  font-weight: bold;
  color: #555;
}

.form-group input,
.form-group select {
  width: 100%;
  padding: 0.75rem;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 1rem;
}

.form-group input:focus,
.form-group select:focus {
  outline: none;
  border-color: #25D366;
}

/* Responsive */
@media (max-width: 768px) {
  .detail-grid {
    grid-template-columns: 1fr;
  }

  .detail-title-row {
    flex-direction: column;
    align-items: flex-start;
  }

  .delete-session-btn {
    width: 100%;
    text-align: center;
  }

  .reconnect-buttons {
    flex-direction: column;
  }

  .reconnect-btn {
    min-width: auto;
  }

  .api-key-box {
    flex-direction: column;
    align-items: stretch;
  }

  .webhook-events-selector {
    grid-template-columns: 1fr;
  }

  .toggle-grid {
    grid-template-columns: 1fr;
  }

  .event-checkbox-enhanced {
    padding: 0.5rem;
  }

  .event-label {
    font-size: 0.9rem;
  }

  .event-description {
    font-size: 0.8rem;
  }
}
