/* NewSessionModal Component Styles */

.form-group {
  margin-bottom: 1.5rem;
}

.form-group label {
  display: block;
  margin-bottom: 0.5rem;
  font-weight: bold;
  color: #555;
}

.form-group input,
.form-group select {
  width: 100%;
  padding: 0.75rem;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 1rem;
  box-sizing: border-box;
}

.form-group input:focus,
.form-group select:focus {
  outline: none;
  border-color: #25D366;
}

.auth-method-selection h3 {
  margin-top: 0;
  margin-bottom: 1rem;
  color: #2a3a5a;
  font-size: 1rem;
}

.auth-methods {
  display: flex;
  gap: 1rem;
  margin-bottom: 1.5rem;
}

.auth-methods button {
  flex: 1;
  padding: 1rem;
  border: 2px solid #ddd;
  border-radius: 4px;
  background: white;
  cursor: pointer;
  font-size: 1rem;
  transition: all 0.3s;
}

.auth-methods button:hover {
  border-color: #aaa;
}

.auth-methods button.selected {
  border-color: #25D366;
  background-color: #e8f7f0;
  color: #25D366;
  font-weight: bold;
}

/* Button styles for modal footer */
.cancel-btn,
.create-btn {
  padding: 0.75rem 1.5rem;
  border: none;
  border-radius: 4px;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s;
}

.cancel-btn {
  background-color: #f5f5f5;
  color: #666;
}

.cancel-btn:hover {
  background-color: #e0e0e0;
}

.create-btn {
  background-color: #25D366;
  color: white;
}

.create-btn:hover {
  background-color: #1fa855;
}
