/* LogChat Page Styles */

.log-chat {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.log-chat h1 {
  color: #2a3a5a;
  margin-bottom: 1.5rem;
}

.log-section {
  background: white;
  border-radius: 8px;
  padding: 1.5rem;
  box-shadow: 0 4px 6px rgba(0,0,0,0.05);
}

.log-section h2 {
  color: #2a3a5a;
  font-size: 1.3rem;
  margin-bottom: 1rem;
  padding-bottom: 0.5rem;
  border-bottom: 2px solid #eee;
}

/* Messages Styles */
.message-list {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.message-item {
  padding: 1rem;
  background-color: #f5f7fa;
  border-radius: 8px;
  border: 1px solid #ddd;
  transition: all 0.2s;
}

.message-item:hover {
  background-color: #e8f5e9;
  border-color: #25D366;
  box-shadow: 0 2px 4px rgba(37, 211, 102, 0.1);
}

.message-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 0.75rem;
  gap: 1rem;
}

.contact-info {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}

.contact-name {
  font-weight: 600;
  color: #333;
  font-size: 0.95rem;
}

.contact-phone {
  font-size: 0.85rem;
  color: #666;
}

.timestamp {
  font-size: 0.85rem;
  color: #999;
  white-space: nowrap;
}

.message-content {
  color: #555;
  line-height: 1.5;
  font-size: 0.95rem;
}

/* Contacts Styles */
.contact-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 1rem;
}

.contact-item {
  padding: 1rem;
  background-color: #f5f7fa;
  border-radius: 8px;
  border: 1px solid #ddd;
  transition: all 0.2s;
}

.contact-item:hover {
  background-color: #e8f5e9;
  border-color: #25D366;
  box-shadow: 0 2px 4px rgba(37, 211, 102, 0.1);
  transform: translateY(-2px);
}

.contact-item .contact-name {
  font-weight: 600;
  color: #333;
  font-size: 1rem;
  margin-bottom: 0.5rem;
}

.contact-item .contact-phone {
  color: #666;
  font-size: 0.9rem;
}

/* Responsive */
@media (max-width: 768px) {
  .contact-list {
    grid-template-columns: 1fr;
  }
}
